@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto:wght@700&display=swap);
h1,
h2,
h3,
h4 {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}

body {
  margin: 0;
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.fade-in {
  animation: fadein 1s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
.skills-container {
  height: 250px;
}
.App-header {
  background-image: url(/dkendrick.github.io/static/media/splash.b954bc56.jpg);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #333;
}

.App-header .title {
  letter-spacing: 2px;
  font-size: 3.5rem;
}

.App-header .subtitle {
  letter-spacing: 4px;
  font-size: 2rem;
}

.App-header h4 {
  letter-spacing: 2px;
  font-size: 1rem;
}
